export default {
  data() {
    return {
      timers: [],
      barControl: '',
    };
  },
  props: {
    options: {
      type: Object,
      default() {
        return {};
      },
    },
    show: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    setStyle() {
      return {
        color: this.options.textColor || '#fff',
        background: this.options.backgroundColor || '#21e7b6',
      };
    },
    setTime() {
      return {
        transition: `all ${(this.options.showTime / 1000) || 3}s linear`,
        background: this.options.barColor || '#03D6D2',
      };
    },
  },
  methods: {
    countdown() {
      if (this.options.autoClose) {
        if (this.options.countdownBar) {
          setTimeout(() => {
            this.barControl = 'count-leave';
          }, 10);
        }
        const t = setTimeout(() => {
          this.close();
        }, this.options.showTime || 3000);
        this.timers.push(t);
      }
    },
    close() {
      this.$emit('close');
    },
  },
  watch: {
    options() {
      this.barControl = '';
      this.timers.forEach((timer) => {
        window.clearTimeout(timer);
      });
      this.timers = [];
      this.countdown();
    },
  },
};



